/**
 * Executes a callback function (`fn`) when the specified DOM element becomes visible
 * in the viewport, using IntersectionObserver. Automatically stops observing after first trigger.
 *
 * Useful for lazy-loading data, components, or animations when an element enters the viewport.
 *
 * @param {Function} importFn - The function to execute when the element becomes visible.
 * @param {string} element - A CSS selector for the element to observe.
 * @param {Array} args - arguments function
 * @param {IntersectionObserverInit} [options={ threshold: 0, rootMargin: '100px' }] - Optional IntersectionObserver settings.
 * @returns {Object} An object with a `stopObserving()` method.
 */
export function useLoadFunction(importFn, element, args = [], options = { threshold: 0, rootMargin: '100px' }) {
    const targetElement = document.querySelector(element)

    if (!targetElement) {
        return {
            stopObserving: () => {},
        }
    }

    const observerOptions = {
        threshold: 0,
        rootMargin: '100px',
        ...options
    }

    const intersectionCallback = async (entries, observer) => {
        for (const entry of entries) {
            if (entry.isIntersecting) {
                observer.unobserve(entry.target)

                const module = await importFn()

                if (!module) {
                    continue;
                }

                const fn = Object.values(module)[0]
                if (typeof fn === 'function') {
                    fn(...args)
                }
            }
        }
    }

    const observer = new IntersectionObserver(intersectionCallback, observerOptions)
    observer.observe(targetElement)

    return {
        stopObserving: () => {
            observer.unobserve(targetElement)
        },
    }
}